import request from '../request'

// 查询用户存在的待办事项的月份日期
export function getBackLogByUser() {
    return request({
        url: '/api/backLog/getBackLogByUser',
        method: 'get',
    });
}

// 查询用户某个日期的待办事项
export function listBackLogByUserAndDate(data) {
    return request({
        url: '/api/backLog/listBackLogByUserAndDate',
        method: 'post',
        data
    })
}

// 新增
export function saveBackLog(data) {
    return request({
        url: '/api/backLog/saveBackLog',
        method: 'post',
        data
    })
}

// 修改
export function updateBackLog(data) {
    return request({
        url: '/api/backLog/updateBackLog',
        method: 'post',
        data
    })
}

// 删除
export function deleteBackLogById(params) {
    return request({
        url: '/api/backLog/deleteBackLogById',
        method: 'get',
        params: params
    });
}
