<template>
  <div id="EChartsView" style="height: 100%">
    <!-- 图表的div标签 ref:方法数据指定名称 id:样式 -->
    <div ref="chart" id="chartDiv" style="height: 100%"></div>
  </div>
</template>

<script>
// 引入echarts的位置
import * as echarts from "echarts";
import {getSysTheme} from "@/util/system";

export default {
  name: 'EChartsView',
  data: function () {
    return {
      // 图表的数据 会根据官网提供数据格式显示对应的图表
      chartData: {},
    };
  },

  // 页面加载会执行此方法进行图表数据赋值初始化显示
  mounted() {
    // this.getDate();
  },

  methods: {
    // 通过获取data值渲染
    setCharts() {
      // 指定图表HTML标签ref值
      if (getSysTheme()) {
        let myEcharts = echarts.init(this.$refs.chart, 'dark');
        chartData.backgroundColor = '#181818';
        // 设置图表参数
        myEcharts.setOption(this.chartData);
      } else {
        let myEcharts = echarts.init(this.$refs.chart, 'light');
        chartData.backgroundColor = '#ffffff';
        // 设置图表参数
        myEcharts.setOption(this.chartData);
      }
    },
    // 直接传递值进行渲染
    setChartsData(chartData) {
      if (getSysTheme()) {
        // 指定图表HTML标签ref值
        let myEcharts = echarts.init(this.$refs.chart, 'dark');
        chartData.backgroundColor = '#181818';
        // 设置图表参数
        myEcharts.setOption(chartData);
      } else {
        // 指定图表HTML标签ref值
        let myEcharts = echarts.init(this.$refs.chart, 'light');
        chartData.backgroundColor = '#ffffff';
        // 设置图表参数
        myEcharts.setOption(chartData);
      }
    }
  },
}
</script>

<!-- 组件样式 -->
<style scoped>
/*图表的样式*/
#chartDiv {
  width: 100%;
  height: 300px;
}
</style>
