import request from '../request'


// 获取用户列表
export function listUserAdmin(data) {
    return request({
        url: '/api/userAdmin/listUserAdmin',
        method: 'post',
        data
    })
}

// 新增用户
export function saveUserAdmin(data) {
    return request({
        url: '/api/userAdmin/saveUserAdmin',
        method: 'post',
        data
    })
}

// 修改用户
export function updateUserAdmin(data) {
    return request({
        url: '/api/userAdmin/updateUserAdmin',
        method: 'post',
        data
    })
}

// 删除
export function deleteUserAdminById(params) {
    return request({
        url: '/api/userAdmin/deleteUserAdminById',
        method: 'get',
        params: params
    });
}

// 根据id查询对象
export function getUserAdminById(params) {
    return request({
        url: '/api/userAdmin/getUserAdminById',
        method: 'get',
        params: params
    });
}

// 根据请求头TOKEN查询用户信息-个人中心查询
export function userGetData() {
    return request({
        url: '/api/userAdmin/userGetData',
        method: 'get',
    });
}

// 修改用户密码
export function updateUserPassword(data) {
    return request({
        url: '/api/userAdmin/updateUserPassword',
        method: 'post',
        data
    })
}

// 重置用户密码 新密码为账号
export function resetPass(data) {
    return request({
        url: '/api/userAdmin/resetPass',
        method: 'post',
        data
    })
}
