import request from '../../request'


// 获取列表
export function listAfter(data) {
    return request({
        url: '/api/after/listAfter',
        method: 'post',
        data
    })
}

// 新增
export function saveAfter(data) {
    return request({
        url: '/api/after/saveAfter',
        method: 'post',
        data
    })
}

// 修改
export function updateAfter(data) {
    return request({
        url: '/api/after/updateAfter',
        method: 'post',
        data
    })
}

// 删除
export function deleteAfterById(params) {
    return request({
        url: '/api/after/deleteAfterById',
        method: 'get',
        params: params
    });
}

// 根据ID查询详情
export function getAfterById(params) {
    return request({
        url: '/api/after/getAfterById',
        method: 'get',
        params: params
    });
}

// 根据售后单号查询售后单流程
export function listAfterFlowBySelcetUrl(data) {
    return request({
        url: '/api/after/listAfterFlowBySelcetUrl',
        method: 'post',
        data
    });
}

// 新增回复数据
export function saveAfterFlow(data) {
    return request({
        url: '/api/after/saveAfterFlow',
        method: 'post',
        data
    });
}

// 结束工单
export function finishAfter(data) {
    return request({
        url: '/api/after/finishAfter',
        method: 'post',
        data
    });
}

// 获取首页统计数据 卡片
export function getStatisticsData() {
    return request({
        url: '/api/after/getStatisticsData',
        method: 'get',
    });
}

// 获取首页图表数据 可视化
export function getHomeCharData(data) {
    return request({
        url: '/api/after/getHomeCharData',
        method: 'post',
        data
    });
}
