import request from '../request'


// 获取列表
export function listMessagePush(data) {
    return request({
        url: '/api/messagePush/listMessagePush',
        method: 'post',
        data
    })
}

// 新增
export function saveMessagePush(data) {
    return request({
        url: '/api/messagePush/saveMessagePush',
        method: 'post',
        data
    })
}

// 修改
export function updateMessagePush(data) {
    return request({
        url: '/api/messagePush/updateMessagePush',
        method: 'post',
        data
    })
}

// 删除
export function deleteMessagePushById(params) {
    return request({
        url: '/api/messagePush/deleteMessagePushById',
        method: 'get',
        params: params
    });
}

// 根据通知分类获取数据 1_客户页 2_后台页
export function listMessageByType(params) {
    return request({
        url: '/api/messagePush/listMessageByType',
        method: 'get',
        params: params
    });
}
