<template>
  <div id="backLog">

    <el-drawer v-model="drawer" direction="rtl" @close="cancelClick">
      <template #header>
        <div class="titleBox">
          {{ $t("HomePageView.backLog.title") }} - {{ fatherData.day }} - {{ fatherData.week }}
        </div>
      </template>
      <template #default>
        <div class="conBox">
          <el-empty v-if="backLogList.length == 0" :description="$t('HomePageView.backLog.description')">
            <el-button type="primary" @click="gotoDialog(null, 'add')" plain>
              {{ $t('HomePageView.backLog.addButton') }}
            </el-button>
          </el-empty>
          <div class="logBox" v-if="backLogList.length > 0" v-for="backLog in backLogList" :key="backLog.id">
            <div class="logTitle">
              <div style="float: left" class="txtBox">
                {{ backLog.title }} -
                <el-tag class="ml-2" type="success" v-if="backLog.enable == 1">
                  {{ $t('HomePageView.backLog.enableTag.pending') }}
                </el-tag>
                <el-tag v-if="backLog.enable == 2">
                  {{ $t('HomePageView.backLog.enableTag.processing') }}
                </el-tag>
                <el-tag class="ml-2" type="info" v-if="backLog.enable == 3">
                  {{ $t('HomePageView.backLog.enableTag.completed') }}
                </el-tag>
                 -
                <el-tag class="ml-2" type="success" v-if="backLog.significance == 1">
                  {{ $t('HomePageView.backLog.significanceTag.mild') }}
                </el-tag>
                <el-tag class="ml-2" type="warning" v-if="backLog.significance == 2">
                  {{ $t('HomePageView.backLog.significanceTag.general') }}
                </el-tag>
                <el-tag class="ml-2" type="danger" v-if="backLog.significance == 3">
                  {{ $t('HomePageView.backLog.significanceTag.important') }}
                </el-tag>
              </div>
              <div class="logbutton">
                <el-button type="primary" @click="gotoDialog(backLog, 'update')" link plain>
                  {{ $t('common.button.modify') }}
                </el-button>
                <el-button type="danger" @click="deleteDialog(backLog)" link plain>
                  {{ $t('common.button.delete') }}
                </el-button>
              </div>
            </div>
            <div class="logtxt">
              <el-input
                  v-model="backLog.content"
                  :autosize="{ minRows: 2, maxRows: 10 }"
                  type="textarea" :readonly="true"
              />
            </div>
          </div>

        </div>
      </template>
      <template #footer>
        <div style="float: left">
          <el-button type="primary" @click="gotoDialog(null, 'add')" plain>
            {{ $t('common.button.add') }}
          </el-button>
          <el-button @click="cancelClick">
            {{ $t('common.button.cancel') }}
          </el-button>
        </div>
      </template>
    </el-drawer>

    <!-- 弹窗表单 -->
    <el-dialog v-model="dialogFormVisible" :before-close="beforeClose" :title="titleName" width="680" draggable>
      <el-form :model="backLogForm" :rules="rules" ref="ruleForm">
        <el-row :gutter="20">
          <el-col :span="16"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('HomePageView.backLog.formDialog.title')" prop="title" :label-width="formLabelWidth">
              <el-input v-model="backLogForm.title" maxlength="16" :placeholder="$t('HomePageView.backLog.formDialog.titlePlaceholder')" autocomplete="off" />
            </el-form-item>
          </el-col>
          <el-col :span="8"><div class="grid-content ep-bg-purple" /></el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="16"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('HomePageView.backLog.formDialog.enable')" :label-width="formLabelWidth">
              <el-select v-model="backLogForm.enable" :placeholder="$t('HomePageView.backLog.formDialog.titlePlaceholder')">
                <el-option :label="$t('HomePageView.backLog.enableTag.pending')" :value=1 />
                <el-option :label="$t('HomePageView.backLog.enableTag.processing')" :value=2 />
                <el-option :label="$t('HomePageView.backLog.enableTag.completed')" :value=3 />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8"><div class="grid-content ep-bg-purple" /></el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="20"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('HomePageView.backLog.formDialog.significance')" :label-width="formLabelWidth">
              <el-radio-group v-model="backLogForm.significance" >
                <el-radio :label="1">
                  {{ $t('HomePageView.backLog.significanceTag.mild')}}
                </el-radio>
                <el-radio :label="2">
                  {{ $t('HomePageView.backLog.significanceTag.general')}}
                </el-radio>
                <el-radio :label="3">
                  {{ $t('HomePageView.backLog.significanceTag.important')}}
                </el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
          <el-col :span="4"><div class="grid-content ep-bg-purple" /></el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="23"><div class="grid-content ep-bg-purple" />
            <el-form-item :label="$t('HomePageView.backLog.formDialog.content')" prop="content" :label-width="formLabelWidth">
              <el-input
                  v-model="backLogForm.content"
                  :autosize="{ minRows: 5, maxRows: 10 }"
                  type="textarea"
                  :placeholder="$t('HomePageView.backLog.formDialog.contentPlaceholder')"
              />
            </el-form-item>
          </el-col>
          <el-col :span="1"><div class="grid-content ep-bg-purple" /></el-col>
        </el-row>

      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="closeButton">
          {{ $t('common.button.cancel') }}
        </el-button>
        <el-button type="primary" @click="backLogAdd"> {{ $t('common.button.confirm') }} </el-button>
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>

import {deleteBackLogById, listBackLogByUserAndDate, saveBackLog, updateBackLog} from "@/http/basics/backLogApi";
import {ElLoading, ElNotification} from "element-plus";
import {getDayOfWeekInChinese, getDayOfWeekInEnglish} from "@/util/DateUtil";

export default {

  name: "backLog",

  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      drawer: false, // 开关
      // 父组件传递过来的数据
      fatherData: {
        day: '',
      },
      // 待办事项列表
      backLogList: [],

      // 弹窗表单 开关
      dialogFormVisible: false,
      titleName: '待办事项',
      type: '',
      formLabelWidth: '80px',
      // 弹窗表单 数据
      backLogForm: {
        title: '',
        content: '',
        enable: 1,
        significance: 1,
      },
      // 表单验证规则
      rules: {
        title: [
          {required: true, message: this.$t('HomePageView.backLog.formDialog.titlePlaceholder'), trigger: 'blur'},
        ],
        content: [
          {required: true, message: this.$t('HomePageView.backLog.formDialog.contentPlaceholder'), trigger: 'blur'},
        ],
      },
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {

  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {

    // 父组件调用子组件的初始化方法
    backLogInit() {
      const currentLanguage = this.$i18n.locale;
      if (currentLanguage == 'zh') {
        this.fatherData.week = getDayOfWeekInChinese(this.fatherData.day)
      } else if (currentLanguage == 'en') {
        this.fatherData.week = getDayOfWeekInEnglish(this.fatherData.day)
      }
      listBackLogByUserAndDate({backlogDate: this.fatherData.day}).then(res => {
        if (res.data.status == 200) {
          this.backLogList = res.data.data;
        }
      })
    },

    // 取消按钮
    cancelClick() {
      this.drawer = false;
      this.$emit('childClick');
    },

    // 弹窗表单 打开
    deleteDialog(backLog) {
      this.$confirm(this.$t('common.dialog.deleteTips') , this.$t('common.dialog.deleteTitle'), {
        confirmButtonText: this.$t('common.button.confirm'), cancelButtonText: this.$t('common.button.cancel'), type: 'warning'
      }).then(() => {
        deleteBackLogById({
          id: backLog.id,
        }).then(res => {
          if (res.data.status == 200) {
            this.backLogInit();
            ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
          }
        })
      }).catch(() => {});
    },
    gotoDialog(row, type) {
      this.dialogFormVisible = true;
      this.type = type;
      if (type == 'add') {
        this.titleName = this.$t('HomePageView.backLog.titleDialog.addTitle');
      } else if (type == 'update') {
        this.titleName =  this.$t('HomePageView.backLog.titleDialog.updateTitle');
        this.backLogForm = row;
      }
    },
    // 弹窗表单 新增 or 修改
    backLogAdd() {
      // 提交校验
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          const loading = ElLoading.service({
            lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
          );
          if (this.type == 'add') {
            // 新增
            this.backLogForm.backlogDate = this.fatherData.day;
            saveBackLog(this.backLogForm).then(res => {
              if (res.data.status == 200) {
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                this.backLogInit();
                this.closeButton();
              }
            }).finally(() => {
              // 请求无论成功/失败 都执行关闭loading
              loading.close();
            });
          } else if (this.type == 'update') {
            // 修改
            updateBackLog(this.backLogForm).then(res => {
              if (res.data.status == 200) {
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                this.backLogInit();
                this.closeButton();
              }
            }).finally(() => {
              // 请求无论成功/失败 都执行关闭loading
              loading.close();
            });
          }
        } else {
          return false;
        }
      });
    },
    // 弹窗表单 关闭
    closeButton() {
      this.backLogInit();
      this.dialogFormVisible = false;
      this.backLogForm = {
        title: '',
        content: '',
        enable: 1,
        significance: 1,
      };
      this.$refs.ruleForm.resetFields(); // 重置表单验证
    },
    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
  }
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.titleBox {
  font-size: 18px;
  font-weight: bold;
  color: #333333;
}
.conBox{
  height: 100%;
  width: 100%;
  /*border: 1px solid #cfd0d3;*/
  /*box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);*/
}
.logBox {
  /*height: 150px;*/
  width: 100%;
  border: 1px solid #cfd0d3;
  /*box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);*/
  padding: 5px;
  margin-bottom: 10px;
}
.logTitle {
  height: 35px;
  width: 98%;
  padding-left: 10px;
  border-bottom: 1px solid #cfd0d3;
  text-align: center;
  line-height: 30px;
  font-size: 15px;
  font-weight: bold;
}

.logbutton {
  float: right;
  margin-right: 10px;
}
.logtxt {
  min-height: 50px;
  width: 98%;
  padding: 10px;
  font-size: 13px;
  line-height: 20px;
  word-break: break-all;
}
</style>
