<script>

import {userGetData} from "@/http/basics/userApi";
import EChartsView from "@/views/common/EChartsView";
import {resourceAndAfterIsUser} from "@/util/resource";
import {getHomeCharData, getStatisticsData} from "@/http/spuOred/afterSpu/afterApi";
import {listMessageByType} from "@/http/clientPage/messageApi";
import {getBackLogByUser} from "@/http/basics/backLogApi";
import backLogView from "@/views/home/BackLogView";
import {getNewYmdhmsStr, getNewYmdStr, offsetDays} from "@/util/DateUtil";
import {getSysLanguage} from "@/util/system";

export default {
  name: 'HomePageView',
  components: {EChartsView, backLogView},
  data: function () {
    return {
      // 更新主页对象
      refreshData: {
        type: 'HomePage',
        herf: '',
      },

      userInfo: {
        newIamge: '',
      },
      userSymbolStr: '', // 用户查询售后单的权限标识 1: 查询所有数据 | 2: 查询部分地区数据 | 3: 查询仅处理人为自己的数据
      currentTime: '', // 新增当前时间属性
      intervalId: null, // 新增定时器id属性

      // 统计数据
      radioStatistics: this.$t('HomePageView.dataPresentation.radio.my'),
      allStatisticsData: {
        threeDayQuantity: 0,
        threeDayRatio: "0%",
        sevenDayQuantity: 0,
        sevenDayRatio: "0%",
        bigSevenDayQuantity: 0,
        bigSevenDayRatio: "0%",
        allQuantity: 0,
        allRatio: "0%",
      },
      myStatisticsData: {
        threeDayQuantity: 0,
        threeDayRatio: "0%",
        sevenDayQuantity: 0,
        sevenDayRatio: "0%",
        bigSevenDayQuantity: 0,
        bigSevenDayRatio: "0%",
        allQuantity: 0,
        allRatio: "0%",
      },

      // 图表数据
      radio: this.$t("HomePageView.chart.radio.year"),
      fromChar: {
        elapsedMonth: -11
      },
      optionChar: {
        title: {
          text: ''
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          // 类别数组 data: ['Email', 'Union Ads', 'Video Ads', 'Direct', 'Search Engine']
          data: []
        },
        grid: {
          left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
              boundaryGap: false,
              // x轴数据 data: ['2022-01', '2022-02', '2022-03']
              data: []
        },
        yAxis: {
          type: 'value'
        },
        // 数据数组 series: [name: 'Email',type: 'line',stack: 'Total',data: [120, 132, 101, 134, 90, 230, 210]}]
        series: []
      },

      // 通知数据
      activities: [],

      // 日历时间初始
      calendar: new Date(),
      // 日历时间数组 有则显示 *
      txtTime: [],
    };
  },
  mounted() {
    // 页面初始化
    this.getDataView();
    // 获取用户查询售后单的权限标识
    this.userSymbolStr = resourceAndAfterIsUser();
    // 获取当前时间 并且每隔一秒更新一次 定时器
    this.intervalId = setInterval(() => {
      this.newTime();
    }, 1000);
  },
  // 在离开当前路由前执行清理工作 例如关闭 WebSocket 连接等
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalId); // 清除定时器
    next(); // 表示允许导航离开，即不进行阻止
  },
  methods: {
    // 页面初始化
    getDataView() {
      // 获取用户信息
      this.getUserInfo();
      // 获取统计数据
      this.getStatisticsData();
      // 获取图表数据 并且注入
      this.getCharData();
      // 获取通知数据
      this.getActivities();
      // 获取用户存在的待办事项的月份日期
      this.getBackLogDateList();
    },
    // 获取当前时间
    newTime() {
      // 更新当前时间 格式化: 2021-01-01 00:00:00
      this.currentTime = getNewYmdhmsStr(new Date());
    },
    // 获取用户信息 卡片
    getUserInfo() {
      userGetData().then(res => {
        if (res.data.status == 200) {
          this.userInfo = res.data.data;
          this.userInfo.newIamge = process.env.VUE_APP_BACKEND_API_URL + res.data.data.photoUrl;
        }
      }).catch((erro) => { });
    },

    // 获取统计数据 卡片
    getStatisticsData() {
      getStatisticsData().then(res => {
        if (res.data.status == 200) {
          this.allStatisticsData = res.data.data.allHomeAfter;
          this.myStatisticsData = res.data.data.myHomeAfter;
        }
      }).catch((erro) => { });
    },
    // 获取统计数据 卡片 点击跳转到售后工单页面并按照条件查询
    gotoAfter(type) {
      // 更新主页对象 标签
      this.refreshData.herf = '/afterSpu';
      this.$emit('childClick', this.refreshData);
      // 根据type判断获取对应条件的数据 工单状态/创建时间/处理人
      // 查询条件 售后工单页面
      let disposeUser = null;
      let afterStatusList = [2, 3];
      let createTimes = [];
      if (type == 'myThreeDay') {
        disposeUser = Number(this.userInfo.id);
        createTimes = [offsetDays(new Date(), -1), getNewYmdStr(new Date())];
      } else if (type == 'mySevenDay') {
        disposeUser = Number(this.userInfo.id);
        createTimes = [offsetDays(new Date(), -6), offsetDays(new Date(), -2)];
      } else if (type == 'myBigSevenDay') {
        disposeUser = Number(this.userInfo.id);
        createTimes = ['2000-01-01', offsetDays(new Date(), -7)];
      } else if (type == 'myAll') {
        disposeUser = this.userInfo.id;
      } else if (type == 'allThreeDay') {
        createTimes = [offsetDays(new Date(), -1), getNewYmdStr(new Date())];
      } else if (type == 'allSevenDay') {
        createTimes = [offsetDays(new Date(), -6), offsetDays(new Date(), -2)];
      } else if (type == 'allBigSevenDay') {
        createTimes = ['2000-01-01', offsetDays(new Date(), -7)];
      } else if (type == 'allAll') { }
      // 跳转到售后工单页面并按照条件查询 传递参数
      this.$router.push({
        path: '/afterSpu',
        query: {
          disposeUser: disposeUser,
          afterStatusList: afterStatusList,
          createTimes: createTimes,
        }
      });
    },

    // 获取图表数据 图表
    radioCharClick() {
      if (this.radio == this.$t('HomePageView.chart.radio.halfYear')) {
        this.fromChar.elapsedMonth = -5;
        this.getCharData();
      } else {
        this.fromChar.elapsedMonth = -11;
        this.getCharData();
      }
    },
    getCharData(){
      let language = getSysLanguage();
        this.fromChar.myStr = this.$t('HomePageView.chart.my');
        this.fromChar.allStr = this.$t('HomePageView.chart.all');
      getHomeCharData(this.fromChar).then(res => {
        // 更新图表数据 图表注入数据
        this.optionChar.legend.data = res.data.data.legendDataList;
        this.optionChar.xAxis.data = res.data.data.xaxisDataList;
        this.optionChar.series = res.data.data.series;
        this.$nextTick(() => {
          // 使用 setOption 更新图表
          this.$refs.charZ.setChartsData(this.optionChar);
        }, 150);
      }).catch((erro) => { });
    },

    // 获取通知数据 通知
    getActivities() {
      listMessageByType({
        type: 2,
      }).then(res => {
        if (res.data.status == 200) {
          this.activities = res.data.data;
        }
      }).catch((erro) => { });
    },

    // 查询用户存在的待办事项的月份日期
    getBackLogDateList() {
      getBackLogByUser().then(res => {
        if (res.data.status == 200) {
          this.txtTime = res.data.data;
        }
      }).catch((erro) => { });
    },
    // 日期点击
    handleDateClick(date) {
      // 点击日期的 年月日 数据
      this.$refs.backLog.fatherData.day = date.data.day;
      this.$refs.backLog.drawer = true;
      this.$refs.backLog.backLogInit()
    },
  },
}
</script>

